@import "../config/colors";

.hero {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: -1; }

.hero__subheader {
  font-size: 28px;
  font-weight: 400;
  margin: 0; }

.hero__header {
  font-weight: 900;
  font-size: 88px;
  margin: 0; }

.hero__directions {
  color: rgba(0, 0, 0, 0.75);
  margin: 4px; }

.hero__input {
  border: 1px solid $color-brand;
  height: 40px;
  margin: 4px; }

.hero__input--amount {
  width: 150px;
  font-size: 18px;
  padding: 8px; }

.hero__input--time {
  width: 160px;
  font-size: 18px;
  padding: 8px; }

.hero__button {
  height: 40px;
  width: 61px;
  margin: 4px;
  background: $color-brand;
  border: none;
  color: $color-white;
  font-size: 18px;
  cursor: pointer; }

.hero__disclaimer {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.75);
  text-align: center;
  margin: 4px; }
